<!--suppress HtmlUnknownTag, CheckEmptyScriptTag, HtmlUnknownAttribute -->
<template>
  <v-container>
<!--    <Notifications :alerts="alerts" />-->
    <v-expansion-panels popout v-model="active">

      <v-expansion-panel hover focusable popout>
        <v-expansion-panel-header>
          Changelog / Table of Contents
        </v-expansion-panel-header>

        <v-expansion-panel-content>
          <h2>Changelog / Table of Contents</h2>
<!--          <p>Recovered article from old website which was lost in the commits</p>-->
          <ul>
            <li v-for="entry in entries">{{ entry.title }}</li><!-- TODO URl magic -->
          </ul>
        </v-expansion-panel-content>
      </v-expansion-panel>

      <v-expansion-panel
        hover focusable popout
        v-for="entry in entries"
        :key="entry.filename">

        <v-expansion-panel-header>
          {{ entry.title }}
        </v-expansion-panel-header>

        <v-expansion-panel-content>
          <BasicMdEntry :sourcePath="createSourcePath(entry.filename)" />
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>

  </v-container>
</template>

<script>
  import axios from 'axios';
  import router from '../plugins/router.js';
  import BasicMdEntry from '../components/BasicMdEntry.vue';

  export default {
    components: {BasicMdEntry},
    props: ['filename'],

    data: () => ({
      active: null,

      entries: [{
          title: 'This Should Never Happen',
          filename: 'ThisShouldNeverHappen'
        }]
    }),

    watch: {
      active(v) {
        let path = '/essay/' + (v>0 && this.entries[v-1].filename || '');
        if (this.$route.path !== path)
          router.replace(path);
      }
    },

    methods: {
      createSourcePath(filename) {
        return this.$localDataUrl + '/essay/' + filename + '.md';
      },

      expandEntryFromUrl() {
        for (let i=0; i<this.entries.length; i++) {
          if (this.entries[i].filename === this.filename) {
            this.active = i+1;
            return;
          }
        }
        this.active = 0;
      }
    },

    mounted () {
      this.expandEntryFromUrl();
    }
  }
</script>

<style>
/* Copied what Vue gave for min-height and added opacity */
.v-expansion-panel-header {
  transition: opacity,min-height .3s cubic-bezier(.25,.8,.5,1);
}
/* Make text in the accordion handle invisible when the entry is showing, because it begins with that very same title */
.v-expansion-panel-header--active {
  opacity: 0;
}
</style>